import * as constants from '../store/Constants';
import { Recipe } from "../store/Recipe";

export enum SortType {
  DEFAULT,
  ALPHABETICAL,
  TIME
}

export const sortRecipeByName = (recipe1: Recipe, recipe2: Recipe) => {
  if (recipe1[constants.Name] < recipe2[constants.Name]) {
    return -1;
  } else if (recipe1[constants.Name] > recipe2[constants.Name]) {
    return 1;
  }
  return 0;
}

export const sortRecipeByTime = (recipe1: Recipe, recipe2: Recipe) => {
  const time1 = parseFloat(recipe1[constants.Time])
  const time2 = parseFloat(recipe2[constants.Time])

  if (time1 < time2) {
    return -1;
  } else if (time1 > time2) {
    return 1;
  }
  return 0;
}